.swiper {
    .swiper-button-next, .swiper-button-prev {
        background: #0183FF;
        border-radius: 50%;
        width: 56px;
        height: 56px;
        top: 65%;
        &:after {
            content: "";
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            width: 24px;
            height: 24px;
        }
    }
    .swiper-button-next {
        &:after {
            background-image: url(/img/icons/swiper-arrow-right.svg);
        }
    }
    .swiper-button-prev {
        &:after {
            background-image: url(/img/icons/swiper-arrow-left.svg);
        }
    }
}

.accordion {
    width: 100%;
    .accordion-item {
        margin-bottom: 12px;
        width: 100%;
        .accordion-item-content {
            display: none;
            width: 100%;
        }
        .accordion-item-header {
            position: relative;
            &:after {
                content: "";
                display: block;
                background-image: url(/img/icons/accodion-arrow-dropdown.svg);
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                width: 18px;
                height: 18px;
                position: absolute;
                right: 30px;
                top: 40%;
            }
        }
    }
    .accordion-item.show {
        .accordion-item-header {
            &:after {
                transform: rotate(180deg);
            }
        }
        .accordion-item-content {
            display: block;
        }
    }
}














header {
    height: 780px;
    position: relative;
    background: linear-gradient(0deg, #0B192F 19.96%, rgba(11, 25, 47, 0) 100%);
    .header-video-back {
        position: relative;
        width: 100%;
        height: inherit;
        z-index: -1;
        img, video {
            height: inherit;
        }
    }
    .header-inner {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;

        .header-inner-wrapper {
            max-width: 1246px;
            width: 100%;
            margin: auto;
        }
    }

    ul {
        padding: 0;
        display: flex;
        li {
            &:not(:last-child) {
                margin-right: 28px;
            }
            a {
                font-weight: 500;
                font-size: 14px;
                color: #FFFFFF;

                &:hover {
